.params-sector {
  padding-top: 11rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 90vh;
  width: 90vw;
  margin: 0 auto;

  .params-title {
    position: relative;
    justify-self: center;
    align-self: center;
    font-weight: bold;
    font-size: 1.5rem;
    margin-bottom: 2rem;
    cursor: pointer;
    color: #55606e;

    &::before {
      content: '';
      position: absolute;
      top: -1.2rem;
      left: -1rem;
      width: 4rem;
      height: 4rem;
      border-bottom: 5px solid #55606c;
      border-left: 5px solid #55606c;
      transform: rotate(45deg);
      border-radius: 50%;
      transition: 0.2s;
    }
    &:hover::before {
        border-radius: 15%;
    }
  }
  .params-header {
    margin-top: 3rem;
  }
}

.params-container {
  height: 100%;
  display: grid;
  position: relative;
  grid-gap: 0.3125rem;
  grid-template-columns: repeat(4, 1fr);

}

;

@media (min-width: 768px) and (max-width: 1280px) {
  .params-container {
    grid-template-columns: repeat(3, 1fr);

    .merch-card-container {
      img {
        width: calc(33vw - 0.8rem * 2 + 5px);
      }
    }

  }
}

@media (min-width: 481px) and (max-width: 768px) {
  .params-sector {
    padding-top: 15rem;
  }
  .params-container {
    grid-template-columns: repeat(2, 1fr);

    .merch-card-container {
      img {
        width: calc(50vw - 0.8rem * 2 - 5px);
      }
    }

  }
}

@media (max-width: 481px) {
  .params-sector {
    padding-top: 15rem;
  }
  .params-container {
    grid-template-columns: repeat(1, 1fr);

    .merch-card-container {
      img {
        width: calc(100vw - 0.8rem * 2 - 5px);
      }
    }

  }
}