.user-menu-container {
  width: 100vw;
  height: 100vh;
  display: grid;
  place-items: center;

  .user-menu {
    display: flex;
    color: #55606c;
    flex-direction: column;
    align-items: center;
    background-color: rgba(211, 214, 217, 0.16);
    border-radius: 0.3rem;
    padding: 12px;
    overflow: scroll;
    height: 60vh;
    width: 80vw;
    box-shadow: rgba(50, 50, 93, 0.25) 0 10px 10px -12px inset, rgba(0, 0, 0, 0.3) 0 8px 46px -118px inset;

    span {
      line-height: 1.6;
      margin: 1rem;
      padding: 1rem;
      text-align: justify;
      max-width: 900px;
      white-space: pre-wrap;
      word-wrap: break-word;
    }

    .firstLetter {
      font-size: 1.5rem;
      margin: 0;
      padding: 0;
    }

    .author {
      align-self: flex-end;
      color: #b2b0b0;
    }

    .header-text {
      text-align: center;
      margin: 0;
      padding: .4rem;
    }

    .sign-out-button_container {
      position: absolute;
      bottom: 8vh;

      &:hover {
        color: #97a4af;
      }
    }
  }
}

